.blog{
    width: 100%;
}
.blog__header{
    width: 97%;
    margin: 0 auto;
}
.blog__grid{
    width: 95%;
    background-color: unset;
    margin: 50px auto 0;
    box-sizing: border-box;
    padding: 20px;
    display: flex;
    flex-wrap: wrap;
    gap: 15px;
    justify-content: center;
    align-items: flex-start;
}

@media (orientation:portrait){
    .blog__grid{
        margin: 15px auto 0;
        gap: 50px;
    }
}