.title-matches {
    width: 100%;
    text-transform: uppercase;
    text-align: center;
    position: relative;
    color: #a39a01;
    font-size: 50px;
    font-family: Teko;
    margin-top: 5%;
}

.title-matches::after {
    content: '';
    position: absolute;
    top: 110%;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 3px;
    background: rgb(163, 154, 1);
    background: radial-gradient(circle, rgba(160, 150, 0, 1) 0%, rgba(160, 150, 0, 0) 100%);
}

.matches {
    width: 95%;
    height: 35vh;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(1, 1fr);
    grid-gap: 5%;
    margin-top: 5%;
}

.classification {
    margin-top: 5%;
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    justify-content: center;
}

@media only screen and (orientation: portrait) {
    .matches {
        height: auto;
        grid-template-columns: repeat(1, 1fr);
        grid-template-rows: repeat(2, 1fr);
        justify-items: center;
        grid-gap: 0;
        margin-bottom: 5%;

    }

    .title-matches {
        width: 90%;
    }

    .classification {
        width: 95%;
    }
}